@import url(https://fonts.googleapis.com/css?family=Nunito);
@import url(https://fonts.googleapis.com/css?family=Vibur);
@font-face {
  font-family: 'neontubes';
  src: url('https://bitbucket.org/kennethjensen/webfonts/raw/fc13c1cb430a0e9462da56fe3f421ff7af72db71/neontubes/neontubes-webfont.eot');
  src: url('https://bitbucket.org/kennethjensen/webfonts/raw/fc13c1cb430a0e9462da56fe3f421ff7af72db71/neontubes/neontubes-webfont.eot?#iefix') format('embedded-opentype'),
       url('https://bitbucket.org/kennethjensen/webfonts/raw/fc13c1cb430a0e9462da56fe3f421ff7af72db71/neontubes/neontubes-webfont.woff2') format('woff2'),
       url('https://bitbucket.org/kennethjensen/webfonts/raw/fc13c1cb430a0e9462da56fe3f421ff7af72db71/neontubes/neontubes-webfont.woff') format('woff'),
       url('https://bitbucket.org/kennethjensen/webfonts/raw/fc13c1cb430a0e9462da56fe3f421ff7af72db71/neontubes/neontubes-webfont.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
html,body,#root {
  width:100%;
  height:100%;
  margin: 0;
  padding: 0;

}
body {
  -webkit-font-smoothing:antialiased;
  -moz-osx-font-smoothing:grayscale;
  background-image:-webkit-linear-gradient(rgba(0,0,0,0.85),rgba(0,0,0,0.85)),url(/Portfolio/static/media/bricks-min.299e78c6.jpg);
  background-image:linear-gradient(rgba(0,0,0,0.85),rgba(0,0,0,0.85)),url(/Portfolio/static/media/bricks-min.299e78c6.jpg);
  background-size: cover;
  box-sizing: border-box;

}
html::-webkit-scrollbar {
  width: 12px;
  }  

  html::-webkit-scrollbar-track {
box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
border-radius: 10px;
}

html::-webkit-scrollbar-thumb {
border-radius: 10px;
box-shadow: inset 0 0 6px rgba(0,0,0,.8); 
}
a,
a:hover,
a:active,
a:focus,
a:visited {
  text-decoration: none;
  color: inherit;
}

.particles {
  position: fixed;
  top:0;
  left:0;
  bottom:0;
  right:0;
  z-index: -1;
  pointer-events: none;
}

@media screen and (max-width: 1400px) {
  body {
    font-size: 15px;
  }
}
@media screen and (max-width: 700px) {
  body {
    font-size: 12px;
  background-image:-webkit-linear-gradient(rgba(0,0,0,0.8),rgba(0,0,0,0.8)),url(/Portfolio/static/media/bricks-mobile.274b59cd.jpg);
  background-image:linear-gradient(rgba(0,0,0,0.8),rgba(0,0,0,0.8)),url(/Portfolio/static/media/bricks-mobile.274b59cd.jpg);
  background-size: cover;
}
}
@media screen and (max-width: 350px) {
  body {
    font-size: 10px;
  }
}


